	module.exports = {"data":{"markdownRemark":{"html":"<p>In the early morning, I read \"The Most Mysterious War\" and read the beginning. I talked about the joint action of the Chinese army and the Burmese army against the KMT remnants. However, I always feel that it is not very well written. The writing is a bit pretentious and grand. I didn’t see the magic in the plain, but I was deliberately writing a big deal. Historical facts are valuable.</p>\n<p>I will quickly write an introduction to the \"93 China Development Report\". The topic of the introduction is: Re-recognizing the world. Same as the entire report. It is divided into six parts, corresponding to international, political, economic, cultural and social. Writing this report has a deeper understanding of the development of Chinese society, and the materials have also grasped a lot.</p>\n<p>On the way back, everyone was discussing and debating, saying that they should compile \"The Prince's Quotations\" and say \": Chang Jian argues, the child does not speak. Jing Sijiu, and then he: the gentleman argues in the heart, the villain argues in the mouth; the gentleman argues in Zhi The villain argues with the words.\"</p>","id":"/home/swain/workspace/political-life/src/pages/en/0022.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0022.md","frontmatter":{"title":"February 1st Tuesday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0022.md absPath of file >>> MarkdownRemark"}}

